<template>
    <div class="user-search">
        <div class="px-0 py-5 px-md-5">
            <h2 class="mb-5">Поиск компании</h2>
            <filter-company></filter-company>
            <div class="row my-5 py-5">
                <company-item class="col-12 mb-5" v-for="(company, index) in companies" :key="index + '-company'"  :company="company"/>
            </div>
        </div>
    </div>
</template>

<script>
  import {COMPANY, USER} from "../../store/mutation-types";
  import {COMPANIES, GET_ALL_COMPANIES, GET_COMPANIES_FLEX} from "../../store/types/companies";
  import {GET_PROFILE, USER_PROFILE} from "../../store/types/userProfile";
  import FilterCompany from "./FilterCompany";
  import CompanyItem from './CompanyItem'

    export default {
      name: 'user-search',
      components: {CompanyItem, FilterCompany},
      data() {
        return {
          USER: USER,
          COMPANY: COMPANY
        }
      },
      computed: {
        companies(){
          return this.$store.getters[COMPANIES + GET_COMPANIES_FLEX]
        },
        isLogged() {
          return this.$store.getters['auth/isLogged'] // Проверка на логин через store
        },
      },
      mounted() {
        if (this.$store.state.companyProfile.status === 'clean') {
          this.$store.dispatch(USER_PROFILE + GET_PROFILE)
        }
        this.$store.dispatch(COMPANIES + GET_ALL_COMPANIES);
      },
      beforeMount() {
        // Временный обход security роутинга, для не зарегестрированных пользователей
        const isLogged = this.$store.getters['auth/isLogged'];
        if (isLogged !== USER) {
          this.$router.push('/')
        }
      }
    }
</script>
