import api, {baseURL} from '../store/api/main'

const studentService = {
  loadProfile(id) {
    return new Promise((resolve, reject) => {
      api.get('student/'+id).then(res => {
        if (res === 'null') {
          throw new Error('Student not found');
        }
        const data = res.data;
        data.student.image = baseURL+'/student/image-avatar/'+id+'.png';
        resolve(data);
      }).catch(err => {
        reject(err);
      })
    });
  },
  loadUserDocs(id) {
    return new Promise((resolve, reject) => {
      api.get('/student/public-documents/'+id)
        .then(res => {
          const files = res.data.files;
          if (!files) {
            throw new Error('No files in response')
          }
          resolve(files);
        }).catch(err => {
          reject(err);
      });
    });
  }
};

export default studentService
